.home-outer-container {
  margin-bottom: 35px;
}

.home-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.home-container > * {
  grid-column: 1 / span 3;
  margin: auto;
  text-align: center;
}

.basic-search-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  background: #2e3238;
}

/* ---------------------------------- */
/* Basic Instructions */

.basic-instructions {
  color: #f8b500;
  font-size: 1.75rem;
  padding-top: 1rem;
  max-width: 675px;
}

.instructions-span {
  border-radius: 50%;
  background-color: #fc3c3c;
  border-radius: 50%;
  height: 50px;
  width: 50px;
}

/* ------------------------------------------- */
/* Search Bar CSS */
.search-bar {
  width: 95%;
  max-width: 1000px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-bottom: 10px;
  margin-top: 10px;
}

.search-input {
  padding: 10px;
  font-size: 17px;
  border: 1px solid #393e46;
  border-radius: 5px 0 0 5px;
  float: left;
  width: 80%;
  background: #f1f1f1;
}

.search-button {
  float: left;
  width: 20%;
  padding: 10px;
  background: #00adb5;
  color: white;
  font-size: 17px;
  border: 1px solid #393e46;
  border-left: none;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
}

.search-button:hover,
.search-button:focus {
  background: #008a8f;
  outline: none;
}

.search-input:focus {
  border: 1px solid #008a8f;
  outline: none;
}


.advanced-search {
  min-width: 165px;
  text-align: right;
  color: #00adb5;
  align-self: flex-end;
}

.advanced-search:hover {
  cursor: pointer;
  color: #008a8f;
}

/* ------------------------------------------ */
/* Icon Selector CSS */
.all-categories {
  width: 100%;
}

.browse-categories {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem;
  background: #393e46;
  color: white;
  cursor: pointer;
  min-height: 40px;
  width: 100%;
}

.browse-categories-bottom {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.5rem;
  background: #393e46;
  color: white;
  min-height: 40px;
  border-top: 1px solid #fff4e0;
}

.browse {
  padding-right: 5px;
}

.categories-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  grid-column: 1 / span 3;
  width: 100%;
}

.icons-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  grid-column: 1 / span 3;
  width: 100%;
}

.icon-container {
  display: flex;
  flex-direction: row;
  background: #2e3238;
  padding: 0.5rem 0.75rem 0.5rem 0.75rem;
  border-top: 1px solid #fff4e0;
  cursor: pointer;
  min-height: 35px;
}

.icon-container:hover {
  background: #85898e;
}

.selected-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0.5rem;
  padding: 0.5rem 0.75rem 0.5rem 0.75rem;
  border-top: 1px solid #fff4e0;
  background: #393e46;
  color: white;
  cursor: pointer;
}

.selected-item > * {
  padding-left: 1rem;
}

.small-icon {
  padding: 0 0.5rem 0 0.5rem;
}

.icon-name {
  color: white;
}

.category-plus {
  margin-left: auto;
  order: 2;
}
/* --------------------------------- */
/* CSS transition for the categories */

.icons-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  grid-column: 1 / span 3;
  width: 100%;
  max-height: 800px;
  overflow: hidden;
  transition: all 500ms ease-in;
}

.browse-drawer-exit {
  max-height: 0px;
}

.browse-drawer-exit-active {
  max-height: 0px;
}

.browse-drawer-exit-done {
  max-height: 0px;
}

.browse-drawer-enter {
  max-height: 800px;
}

.browse-drawer-enter-active {
  max-height: 800px;
}

.browse-drawer-enter-done {
  max-height: 500px;
  transition: all 300ms ease-out;
}
/* ------------------------------------------ */
/* Advanced Search Modal CSS */

.modal {
  position: fixed;
  top: 55px;
  bottom: 30px;
  left: 0;
  right: 0;
  display: grid;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.3);
  overflow-y: scroll;
  z-index: 10;
}

.modal-box {
  padding: 20px;
  background-color: #faf9f8;
  border-radius: 5px;
  display: inline-block;
  margin: 1rem;
  position: relative;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  justify-self: center;
}

.modal-search-heading-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: auto;
  width: 100%;
}

.search-nav-container-1,
.search-nav-container-2 {
  display: flex;
}

.search-nav-container-2 {
  justify-self: end;
}

.search-nav-container-1 {
  color: #393e46;
  align-items: center;
  justify-content: center;
}

.modal-heading-title {
  color: #393e46;
  font-size: 1.75rem;
  font-weight: 500;
}

.modal-heading-spacer {
  flex: 1 1;
}

.modal-heading-divider {
  display: block;
  border-left: 1px solid #ccc;
  height: 25px;
  margin-right: 10px;
}

.modal-heading-button {
  display: table;
  border: none;
  border-radius: 5px;
  background-color: #00adb5;
  padding: 10px 15px 10px 15px;
  margin-right: 10px;
  font-size: 16px;
  color: white;
  cursor: pointer;
  text-align: center;
  height: auto;
}

.modal-heading-button:hover,
.modal-heading-button:focus {
  cursor: pointer;
  background: #008a8f;
}

.modal-search-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
}

/* the container for set of checkboxes */

.modal-search-item-title {
  font-size: 1.1rem;
  font-weight: 500;
  color: #393e46;
  margin-bottom: 10px;
}

.modal-search-organization,
.modal-search-category {
  display: block;
  border: none;
  margin-right: 10px;
  font-size: 16px;
  cursor: pointer;
  text-align: center;
}

.modal-search-category {
  padding: 10px 5px 10px 15px;
}

.modal-search-organization {
  padding: 10px 15px 10px 5px;
}

.advanced-container {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 8px;
  cursor: pointer;
  font-size: 1rem;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: #393e46;
}

/* Hide the browser's default checkbox */
.advanced-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 16px;
  width: 16px;
  background-color: #eee;
}

input[type='checkbox'] {
  -webkit-appearance: checkbox;
}

/* On mouse-over, add a grey background color */
.advanced-container:hover input ~ .checkmark {
  background-color: #ccc;
}

/* When the checkbox is checked, add a blue background */
.advanced-container input:checked ~ .checkmark {
  background-color: #00adb5;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: '';
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.advanced-container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.advanced-container .checkmark:after {
  left: 6px;
  top: 3px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  transform: rotate(45deg);
}

