    max-width: 955px;
    min-width: 835px;
  }

  .search-bar {
    grid-column: 1 / span 3;
    grid-row: 2;
    width: 80%;
  }
  
  .submit {
    grid-column: 1 / span 3;
  }

  .text-span {
    font-size: 2.5rem;
  }

  /* ------------------------------ */
  .rcrOnlineLogo-enter-done {
    width: 250px;
    height: 250px;
  }

  .rcrOnlineLogo-enter {
    width: 250px;
    height: 250px;
  }

  .rcrOnlineLogo-enter-active {
    width: 250px;
    height: 250px;
  }

  .rcrOnlineLogoText-enter {
    font-size: 1.25rem;
  }

  .rcrOnlineLogoText-enter-active {
    font-size: 2rem;
  }

  .rcrOnlineLogoText-enter-done {
    font-size: 2rem;
  }
  /* --------------------------------- */

  .categories-container {
    width: 100%;
    min-width: 845px;
    background: #2e3238;
  }

  .icons-container {
    grid-column: 1 / span 3;
    display: flex;
    justify-content: center;
    flex-direction: row;
  }

  .icon-container {
    display: flex;
    flex-direction: column;
    background: #2e3238;
    padding: 0.5rem;
    text-align: center;
    align-content: space-evenly;
    width: 95px;
  }

  .icon-name {
    padding-top: 5px;
    color: white;
  }

  .icons-dropdown-container {
    display: flex;
    flex-direction: column;
    /* below are the new properties
    to deal with div not pushing down
    results (cards, map) */
    position: absolute;
    width: 100%;
    margin-top: 90px;
    z-index: 3;
    box-shadow: 0 5px 6px 0 #777;
  }

  .icon-dropdown-container {
    text-align: left;
    border-top: 1px solid white;
    padding: 0.5rem;
    background: #393e46;
    cursor: pointer;
  }

  .icon-dropdown-container:hover {
    background: #85898e;
  }

  .icon-dropdown-name {
    padding-left: 30px;
