/* ------------------------------------- */
/* Logo CSS */

.rcr-online {
  visibility: hidden;
  display: none;
}

.rcrOnlineLogo {
  background-color: #fc3c3c;
  border-radius: 50%;
  height: 170px;
  width: 170px;
  text-align: center;
  margin-right: 1rem;
  margin-top: 1rem;
  box-shadow: 0 5px 6px 0 #777;
}

.rcrOnlineLogoText {
  color: #fff4e0;

  font-family: 'Ultra', serif;
  height: auto;
  margin: 0 auto;
  padding: 1.75rem 1.25rem 1.25rem 1.25rem;
  position: relative;
}

.rcrOnlineLogo-enter {
  opacity: 0.01;
}

.rcrOnlineLogo-enter-active {
  opacity: 1;
  transition: all 500ms ease-out;
}

.rcrOnlineLogoText-enter {
  transform: scale(0.5) translateY(-30%);
  opacity: 0.01;
  width: 15%;
}

.rcrOnlineLogoText-enter-active {
  opacity: 1;
  transform: scale(1) translateY(0%);
  transition: all 500ms ease-out;
  font-size: 1.25rem;
  width: 95%;
}

.rcrOnlineLogoText-enter-done {
  font-size: 1.25rem;
  width: 95%;
}

.text-span {
  color: #00adb5;
  text-shadow: -1px -1px 0 #fff4e0, 1px -1px 0 #fff4e0, -1px 1px 0 #fff4e0,
    1px 1px 0 #fff4e0;
  font-family: sans-serif;
  font-size: 1.5rem;
}

