  .map-container {
    grid-column: 2 / span 2;
    grid-row-start: 1;
    position: -webkit-sticky;
    position: sticky;
    top: 65px;
    height: 68vh;
    margin-top: 5px;
    padding: 0;
  }

  .print-details-button {
    display: inline-block;
    color: black;
  }

  .print-button-container {
    visibility: visible;
    display: flex;
    flex-direction: row-reverse;
    position: relative;
    bottom: 60px;
    right: 5px;
    z-index: 999;
  }
  .print-button {
    margin: 5px 5px 30px 5px;
    color: rgba(0, 0, 0, 0.9);
  }

  .print-button,
  .print-details-button {
    margin: 5px;
    padding: 5px;
    font-size: 1rem;
    border-radius: 5px;
    background-color: #fff;
    border: 2px solid rgba(0, 0, 0, 0.2);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }

  .print-button:hover,
  .print-details-button:hover,
  .popup-show-details:hover {
    background-color: #ecedee;
  }

  .print-button:enabled,
  .print-details-button:enabled,
  .popup-show-details:enabled {
    background: linear-gradient(#2e3238 #393e46);
    text-decoration: none;
  }

  .print-button:active,
  .print-details-button:active,
  .popup-show-details:active {
    transform: translateY(1px);
  }
}

@media (min-width: 1200px) {
  .details-inner-container {
    padding: 0 30px 0 30px;
    margin: auto;
    width: 75%;
  }
}

