.nav {
  background: #393e46;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 3;
}

.nav-container {
  height: 56px;
  width: 100%;
  display: flex;
  align-items: center;
}

.sr-logo {
  padding-left: 0.5rem;
  max-width: 120px;
}

.rcr-logo {
  padding-left: 0.5rem;
  max-width: 200px;
}

.spacer {
  flex: 1 1;
}

.nav-item {
  color: white;
  height: 100%;
  padding: 20px 15px 0 15px;
  transition: color 0.2s ease-out;
}

.nav-item:hover {
  color: #f8b500;
}

.nav-item-active {
  background: #85898e;
  color: #f8b500;
  border-top: 2px solid #f8b500;
}

.hamburger-button {
  color: white;
  padding: 10px 20px 0 0;
}

.nav-drawer-item {
  color: white;
  padding: 10px 15px 10px 5px;
  transition: color 200ms ease-in;
}

.nav-drawer-item:hover {
  color: #f8b500;
}

.nav-drawer-item-active {
  background: #85898e;
  color: #f8b500;
  border-top: 2px solid #f8b500;
}

.navdrawer {
  display: none;
  height: 0px;
}

.navdrawer-exit {
  visibility: hidden;
  display: flex;
  flex-direction: column;
  height: 80px;
}

.navdrawer-exit-active {
  visibility: hidden;
  display: flex;
  flex-direction: column;
  height: 0px;
  transition: all 200ms linear;
}

.navdrawer-exit-done {
  display: none;
  height: 0px;
}

.navdrawer-enter {
  display: flex;
  visibility: hidden;
  height: 0px;
}

.navdrawer-enter-active {
  display: flex;
  flex-direction: column;
  height: 80px;
  transition: all 200ms linear;
}

.navdrawer-enter-done {
  display: flex;
  flex-direction: column;
  height: 80px;
}


.footer {
  background: #393e46;
  bottom: 0;
  left: 0;
  right: 0;
  position: fixed;
  height: 30px;
  width: 100%;
  box-shadow: 0 -3px 6px 0 #777;
  z-index: 2;
}

.footer-container {
  display: flex;
  height: 100%;
  justify-content: flex-end;
  align-items: center;
}

.footer-item {
  padding-right: 1rem;
  color: white;
  font-size: 0.75rem;
  max-width: 100px;
}

.mac-logo {
  max-width: 250px;
}

.footer-item:hover {
  color: #f8b500;
}

.revision-date {
  padding-left: 1rem;
  margin-right: 5px;;
  font-size: 0.6rem;
  color: white;
  min-width: 72px;
}

