/* This is to make sure that 
embedded components behave in 
the content box way.
 */
*,
*:before,
*:after {
  box-sizing: inherit;
}

/* set max width for images
so they are never wider than page*/
img {
  max-width: 100%;
}

ul {
  padding: 0;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
}
.main-content {
  background-color: #85898e;
}

